import { createRouter, createWebHistory } from 'vue-router'
import HomeView from '../views/HomeView.vue'
import LoginEmployeepage from '../views/LoginEmployeepage.vue'
import LoginClientpage from '../views/LoginClientpage.vue'
import dashboard from '../views/dashboard/dashboard.vue'
import Page1 from '../components/Page1.vue'
import HomeClient from '../views/client/HomeClient.vue'

const routes = [
  {
    path: '/',
    name: 'home',
    component: HomeView
  },
  {
    path: '/about',
    name: 'about',
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () => import(/* webpackChunkName: "about" */ '../views/AboutView.vue')
  },{

    path: '/loginEmployee',
    name: 'Login Employee Page',
    component: LoginEmployeepage
  },
  {
    path: '/loginClient',
    name: 'Login Client Page',
    component: LoginClientpage
  },{
    path: '/homeClient',
    name: 'home Client Page',
    // props: true ,
    component:HomeClient
  },{
    path: '/dashboard',
    name: 'dashboard',
    props: true ,
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component:dashboard
  },{
    path: '/dashboard/:token',
    name: 'dashboard',
    props: true ,
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component:dashboard
  },{
    path: '/dashboard/settings',
    name: 'Page1',
    props: true ,
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component:Page1,
  },{
    path: '/dashboard/:token',
    name: 'dashboard',
    props: true ,
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component:dashboard
  },{
    path: '/dashboard/:token',
    name: 'dashboard',
    props: true ,
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component:dashboard
  }
]

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes
})

export default router
